}
.hero__nav .btn--outline {
  border: 0.0625rem solid rgba(255, 255, 255, 0.4);
  color: #fff;
  background: transparent;
}
.hero__nav .btn--outline:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-0.0625rem);
}

