/* ===== STACK SECTION ===== */
.stack {
  padding: 6.25rem 5rem;
  background: #fff;
  /* LEFT MENU */
  /* CENTER LINE */
  /* RIGHT CONTENT */
}
.stack__container {
  display: grid;
  grid-template-columns: 1fr 2.5rem 1.3fr;
  gap: 3.75rem;
  align-items: center;
}
.stack__menu {
  list-style: none;
  padding: 0;
}
.stack__menu li {
  font-size: 1.125rem;
  padding: 0.875rem 0;
  cursor: pointer;
  color: #d0d0d0;
}
.stack__menu li.active {
  background: #d81b60;
  color: #fff;
  padding: 0.625rem 1.125rem;
  border-radius: 0.25rem;
  display: inline-block;
}
.stack__center {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.stack__line {
  position: relative;
  width: 0.125rem;
  height: 18.75rem;
  background: #e5e5e5;
  overflow: hidden;
}
.stack__line-fill {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  background: #d81b60;
  transition: height 0.35s ease;
}
.stack__indicator {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 0.375rem;
  height: 4.9375rem;
  background: #d81b60;
  border-radius: 0.1875rem;
  transition: top 0.35s ease;
}
.stack__counter {
  display: flex;
  margin-top: 1.25rem;
  font-size: 0.875rem;
  color: #7b3fe4;
  fontallowweight: 500;
  text-align: center;
}
.stack__card {
  display: none;
}
.stack__card.active {
  display: block;
}
.stack__card img {
  max-width: 26.25rem;
  border-radius: 0.75rem;
  margin-bottom: 1.5rem;
}
.stack__card h3 {
  font-size: 1.75rem;
  margin-bottom: 0.625rem;
}
.stack__card .tags {
  font-size: 0.875rem;
  color: #7b3fe4;
  margin-bottom: 0.875rem;
  display: flex;
  gap: 1.25rem;
}
.stack__card .desc {
  font-size: 0.9375rem;
  line-height: 1.6;
  color: #555;
  padding-bottom: 1.25rem;
}

