.case-study .btn:hover {
  background: #f0f0f0;
  transform: translateY(-2px);
}
.case-study__stack {
  position: relative;
}
.case-study__card {
  position: sticky;
  top: 50vh;
  transform: translateY(-50%);
  height: 31.25rem;
  width: 100%;
  max-width: 62.5rem;
  margin: 0 auto 2.5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: #fff;
  color: #111;
  border-radius: 1.25rem;
  overflow: hidden;
  box-shadow: 0 1.5625rem 3.125rem rgba(0, 0, 0, 0.3);
  opacity: 1;
  z-index: 1;
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  box-sizing: border-box;
  /* Responsive adjustments */
  /* ACTIVE CARD - FULL VISIBILITY */
  /* NEXT CARD - Slightly visible, coming up */
  /* PREVIOUS CARD - Fading out above */
  /* FAR CARDS - Barely visible */
}
@media (max-width: 1100px) {
  .case-study__card {
    max-width: 90%;
    height: 28.125rem;
  }
}
@media (max-width: 768px) {
  .case-study__card {
    grid-template-columns: 1fr;
    height: auto;
    max-width: 95%;
  }
}
.case-study__card.is-active {
  opacity: 1 !important;
  z-index: 10;
  transform: translateY(-50%) scale(1) !important;
  box-shadow: 0 2.5rem 5rem rgba(0, 0, 0, 0.4);
}
.case-study__card.is-next {
  opacity: 0.4;
  z-index: 5;
  transform: translateY(calc(-50% + 120px)) scale(0.92);
}
@media (max-width: 768px) {
  .case-study__card.is-next {
    transform: translateY(calc(-50% + 100px)) scale(0.92);
  }
}
.case-study__card.is-prev {
  opacity: 0.2;
  z-index: 3;
  transform: translateY(calc(-50% - 120px)) scale(0.88);
}
@media (max-width: 768px) {
  .case-study__card.is-prev {
    transform: translateY(calc(-50% - 100px)) scale(0.88);
  }
}
.case-study__card.is-far {
  opacity: 0.05;
  transform: translateY(-50%) scale(0.8);
}
.case-study__card-left {
  padding: 3.75rem 3.125rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
@media (max-width: 1100px) {
  .case-study__card-left {
    padding: 2.5rem 1.875rem;
  }
}
@media (max-width: 768px) {
  .case-study__card-left {
    padding: 2.5rem 1.875rem 1.875rem;
    order: 2;
  }
}
.case-study__card-left .count {
  font-size: 0.875rem;
  color: #999;
  margin-bottom: 1rem;
  font-weight: 500;
  letter-spacing: 0.0625rem;
}
.case-study__card-left h3 {
  font-size: 2rem;
  margin: 0 0 1.25rem 0;
  line-height: 1.2;
  font-weight: 700;
  color: #111;
}
@media (max-width: 1100px) {
  .case-study__card-left h3 {
    font-size: 1.75rem;
  }
}
.case-study__card-left .highlight {
  font-size: 1.125rem;
  line-height: 1.6;
  color: #555;
  margin: 0 0 1.875rem 0;
  font-weight: 400;
}
@media (max-width: 1100px) {
  .case-study__card-left .highlight {
    font-size: 1rem;
  }
}
.case-study__card-left .link {
  font-size: 0.875rem;
  color: #ff4fa3;
  text-decoration: none;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s ease;
}
.case-study__card-left .link:hover {
  color: #e03d8c;
  gap: 0.75rem;
}
.case-study__card-left .link::after {
  content: "→";
  transition: transform 0.3s ease;
}
.case-study__card-left .link:hover::after {
  transform: translateX(4px);
}
.case-study__card-right {
  background: linear-gradient(135deg, #111, #2b2b2b);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2.5rem;
  position: relative;
  overflow: hidden;
}
@media (max-width: 1100px) {
  .case-study__card-right {
    padding: 1.875rem;
  }
}
@media (max-width: 768px) {
  .case-study__card-right {
    padding: 1.875rem;
    min-height: 15.625rem;
    order: 1;
  }
}
.case-study__card-right img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 0.5rem;
  height: auto;
  object-fit: contain;
  box-shadow: 0 0.625rem 1.875rem rgba(0, 0, 0, 0.3);
  transition: transform 0.5s ease;
}
.is-active .case-study__card-right img {
  transform: scale(1.05);
}

