  text-decoration: none;
  font-weight: 500;
}
.playbook__bottom {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 2rem;
}
.playbook__card {
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 0.625rem 1.875rem rgba(0, 0, 0, 0.08);
}
.playbook__card--stats {
