}
.playbook__card--image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.playbook__card--image .overlay-text {
  position: absolute;
  left: 1.5rem;
  bottom: 1.5rem;
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 600;
}

