.testimonial-section {
  padding: 6.25rem 0;
  background: radial-gradient(circle at top right, #b84cff, #0b0616);
  color: #fff;
  position: relative;
  overflow: hidden;
}
.testimonial-section .testimonial__bg {
  position: absolute;
  inset: 0;
  z-index: 0;
  display: block;
  width: 100%;
  height: 100%;
}
.testimonial-section .testimonial__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.testimonial-section .label {
  color: #c89bff;
  font-size: 14px;
}
.testimonial-section h2 {
  font-size: 36px;
  line-height: 1.3;
  margin: 10px 0 20px;
}
.testimonial-section .sub {
  font-size: 14px;
  color: #bfb3d6;
  max-width: 400px;
}
.testimonial-section .test-cont {
  display: flex;
}
.testimonial-section .test-cont .left .clients {
  margin-top: 50px;
  position: relative;
}
.testimonial-section .test-cont .left .clients .client {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 35px;
  opacity: 0.4;
  cursor: pointer;
  transition: all 0.4s ease;
}
.testimonial-section .test-cont .left .clients .client img {
  width: 54px;
  height: 54px;
  border-radius: 50%;
  border: 2px solid transparent;
}
.testimonial-section .test-cont .left .clients .client h4 {
  margin: 0;
  font-size: 15px;
}
.testimonial-section .test-cont .left .clients .client span {
  font-size: 12px;
  color: #b9a9d9;
}
.testimonial-section .test-cont .left .clients .client.active {
  opacity: 1;
}
.testimonial-section .test-cont .left .clients .client.active img {
  border-color: #ff4ecd;
}
.testimonial-section .test-cont .right {
  flex: 1;
  position: relative;
}
.testimonial-section .test-cont .right .testimonial {
  display: none;
  animation: fadeUp 0.6s ease;
}
.testimonial-section .test-cont .right .testimonial.active {
  display: block;
}
.testimonial-section .test-cont .right .testimonial p {
  font-size: 16px;
  line-height: 1.8;
  max-width: 520px;
}
.testimonial-section .test-cont .right .testimonial .brand {
  margin-top: 30px;
  font-weight: bold;
  color: #ff4ecd;
  letter-spacing: 2px;
}

@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
